<template>
  <!--  主叫号码-->
  <div class="calling-number">
    <div class="form-container">
      <el-input
        v-model="paramsData.phone"
        placeholder="输入主叫号码"
        class="number"
        clearable
      >
        <template #suffix>
          <el-icon class="el-input__icon" @click="getList">
            <search />
          </el-icon>
        </template>
      </el-input>
      <!--      <el-select-->
      <!--        v-model="paramsData.whiteStatus"-->
      <!--        clearable-->
      <!--        placeholder="请选择加白状态"-->
      <!--      >-->
      <!--        <el-option-->
      <!--          v-for="item in callingWhiteStatusList"-->
      <!--          :key="item.value"-->
      <!--          :label="item.label"-->
      <!--          :value="item.value"-->
      <!--        />-->
      <!--      </el-select>-->
      <el-button type="primary" @click="getList">查询</el-button>
      <el-button
        v-if="routeQuery.businessType === '1'"
        type="primary"
        @click="handleImportFile"
        >导入号码
      </el-button>
      <!--      <el-button type="primary" @click="handleCallerNum">新增主叫</el-button>-->
    </div>
    <div class="table-container">
      <!--   列表   -->
      <el-table :data="pageData.list" height="calc(100vh - 308px)">
        <el-table-column
          prop="phone"
          label="主叫号码"
          align="center"
        ></el-table-column>
        <!--        <el-table-column-->
        <!--          prop="business"-->
        <!--          label="业务场景"-->
        <!--          align="center"-->
        <!--        ></el-table-column>-->
        <el-table-column
          prop="isp"
          label="运营商"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="city"
          label="归属地"
          align="center"
        ></el-table-column>
        <!--        <el-table-column label="验证状态" align="center">-->
        <!--          <template #default="{ row }">-->
        <!--            <el-tag :type="row.authStatusStyleType">{{-->
        <!--              row.authStatusName-->
        <!--            }}</el-tag>-->
        <!--          </template>-->
        <!--        </el-table-column>-->
        <el-table-column label="加白状态" align="center">
          <template #default="{ row }">
            <el-tag v-if="row.statusName" :type="row.statusType">{{
              row.statusName
            }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="founder"
          label="创建人"
          align="center"
        ></el-table-column>
        <el-table-column prop="createTime" label="创建时间" align="center">
        </el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无数据"></TableEmpty>
        </template>
      </el-table>
      <!--   分页   -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      >
      </PagePagination>
    </div>
    <CustomImportFile
      ref="importFileRef"
      @emitFunc="getList"
    ></CustomImportFile>
    <!--    <AddCallerNumberDialog-->
    <!--      v-model:visible="callerNumber.visible"-->
    <!--      @emitFunc="getList"-->
    <!--    ></AddCallerNumberDialog>-->
  </div>
</template>
<script lang="ts" setup>
import { ref, shallowReactive, defineAsyncComponent } from "vue";
import { Search } from "@element-plus/icons-vue";

import { serviceName } from "@assets/common";
import { LocalStorage, usePage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";
// import { callingWhiteStatusList } from "@assets/typeStatus";

import { downloadFile } from "@/api/download-api";
import { callerNumberListApi } from "@api/business-management-api";

const { bd_userInfo: userInfo } = LocalStorage.batchGetItem([
  "bd_userInfo",
  "bd_virCompanyId",
]);
const CustomImportFile = defineAsyncComponent(
  () => import("@/components/common/custom-import-file.vue")
);
// const AddCallerNumberDialog = defineAsyncComponent(
//   () =>
//     import(
//       "./add-caller-number-dialog.vue"
//     )
// );
// const callerNumber = shallowReactive({
//   visible: false,
// });
const { routeQuery } = useVueRouter();

const importFileRef = ref();
const paramsData = shallowReactive({
  phone: "",
  // whiteStatus: "",
  // code: routeQuery.value.code,
});
const { getList, pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: callerNumberListApi,
  props: {
    pageNumber: "pageNo",
    pageSize: "pageSize",
  },
  paramsData:
    routeQuery.value.businessType === "1"
      ? Object.assign(paramsData, {
          code: routeQuery.value.code,
        })
      : Object.assign(paramsData, {
          code: routeQuery.value.companyId,
        }),
});

function handleImportFile() {
  importFileRef.value.trigger(true, {
    data: {
      companyId: routeQuery.value.companyId,
      profileId: userInfo.profileId,
      userName: userInfo.realName,
    },
    limit: 1,
    autoUpload: false,
    accept: ".xls,.xlsx",
    actionUrl: `${serviceName.callRest}/agentInterface/importData`,
    templateUrl: () => {
      return downloadFile({
        api: `${serviceName.callRest}/agentInterface/templateDownload`,
      });
    },
  });
}

// function handleCallerNum() {  /call-rest/agentInterface/importData
//   callerNumber.visible = true;
// }
</script>
<style lang="scss" scoped>
.form-container {
  justify-content: flex-end;

  .el-input {
    width: 200px;
    margin-right: 10px;
  }

  .el-select {
    width: 200px;
    margin-right: 10px;
  }
}
</style>
